import React, { Component } from 'react'
import './Equipment.css'
import {Link} from 'react-router-dom'
import axios from 'axios'
import authService from '../../services/auth-service';
import { API_URL } from '../../services/url';
export class ViewEquipment extends Component {
    constructor(props)
    {
        super(props)
        this.state = {
              userdata : [],
              skip: 0,
              limit: 3
        }
        this.deletehandler = this.deletehandler.bind(this)
        this.nextpagehandler = this.nextpagehandler.bind(this);
        this.prevpagehandler = this.prevpagehandler.bind(this);
      //this.handleclick = this.handleclick(this);
    }
    prevpagehandler() {
      console.log("paginationhandler");
      // this.setState({
      //   skip: this.state.skip + this.state.limit
      // });\
      this.state.skip = this.state.skip - this.state.limit;
      let limit = this.state.limit;
      let dataskip = this.state.skip;
      console.log(dataskip);
      console.log(`${API_URL}/company/list`, {
        params: { skip: dataskip, limit: limit }
      });
      axios
        .get(
          `${API_URL}/requirement/list`,
          { params: { skip: this.state.skip, limit: this.state.limit } },
          (axios.defaults.headers.common["w_auth"] = authService.getToken())
        )
        .then(response => {
          console.log(response.data);
          const data = response.data;
          this.setState({ userdata: data });
        })
        .catch(error => {
          console.log(error);
        });
    }
    nextpagehandler() {
      console.log("paginationhandler");
      // this.setState({
      //   skip: this.state.skip + this.state.limit
      // });\
      this.state.skip = this.state.skip + this.state.limit;
      let limit = this.state.limit;
      let dataskip = this.state.skip;
      console.log(dataskip);
      console.log(`${API_URL}/company/list`, {
        params: { skip: dataskip, limit: limit }
      });
      axios
        .get(
          `${API_URL}/requirement/list`,
          { params: { skip: dataskip, limit: limit } },
          (axios.defaults.headers.common["w_auth"] = authService.getToken())
        )
        .then(response => {
          console.log(response.data);
          const data = response.data;
          this.setState({ userdata: data });
        })
        .catch(error => {
          console.log(error);
        });
    }
    async componentWillMount() {
        await this.handleclick();
    }
    handleclick() {        
      axios.get(`${API_URL}/equipment/list`,axios.defaults.headers.common['w_auth'] = authService.getToken())
      .then(async response => {
          console.log(response)
          const data = response.data;
         this.setState({ userdata: data });
          console.log('userdata')
          console.log(this.state.userdata)
          })

    }
  async deletehandler(item) {
    await this.setState({
      _id: item._id,
    });
    console.log(item);
    const response = await axios.delete(`${API_URL}/equipment/delete`, { data: { _id: item._id }} , axios.defaults.headers.common['w_auth'] = authService.getToken() )
        console.log(response)
        if (response.data.n===1){
          alert("Equipment has been deleted");
          this.handleclick();
        }
    }
    

 render() {
  let buttondisplay;
  if (this.state.skip == 0) {
    buttondisplay = (
      <div>
        <button
          type="button"
          className="btn btn-info"
          onClick={this.prevpagehandler.bind(this)}
          disabled
        >
          Previous
        </button>
        <button
          type="button"
          className="btn btn-info"
          onClick={this.nextpagehandler.bind(this)}
        >
          Next
        </button>
      </div>
    );
  } else {
    buttondisplay = (
      <div>
        <button
          type="button"
          className="btn btn-info"
          onClick={this.prevpagehandler.bind(this)}
        >
          Previous
        </button>
        <button
          type="button"
          className="btn btn-info"
          onClick={this.nextpagehandler.bind(this)}
        >
          Next
        </button>
      </div>
    );
  }
   let sr=0;
   let equipmentdata;
   if(this.state.userdata.length === 0)
   {
     equipmentdata = (
       <div>

         <center><h4><strong>There is No Equipment Yet.</strong></h4></center>
       </div>
     )
   }
   else{
     equipmentdata = (
       
       <div>
          <table className="table table-bordered table-responsive-md">
     <thead className="thead-dark">
       <tr>
         <th scope="col">Sr. No</th>
               <th scope="col">Equipment Name</th>
               <th scope="col">Capacity</th>
         <th scope="col">Quantity</th>
         <th scope="col">Location</th>
         <th scope="col">Pin Code</th>
         <th scope="col">Action</th>
       </tr>
     </thead>
     <tbody>
     {
      
             this.state.userdata.length ?
                 this.state.userdata.map(function (item, _id) {
                  let name = item.subcategory ? item.subcategory.name : "no data"
               return(
                
       <tr key = {_id}>
         {/* {/* <td>{item.name || "NO DATA"}</td> */}
         <td>{++sr }</td> 
                   <td>{name }</td>
                   <td>{item.capacity || "Unknown"}</td>
         <td>{item.quantity || "1"}</td>
        <td>{item.location || "NO DATA"}</td>
        <td>{item.pinCode || "NO DATA"}</td>
        <td>
        {/* <button type="button" className="btn btn-warning custom-edit-btn custom-btn" onClick = {this.edithandler.bind(this)}><i className="icon-pencil icons"></i></button> */}
        <button type="button" className="btn btn-danger custom-btn" onClick = {this.deletehandler.bind(this,item)}><i className="icon-trash icons"></i></button>
        </td>
               </tr>
     )
             }, this
     )
             :
             <span>No Data Present....</span>
           }
     </tbody>
   </table>
       </div>
     )
   }
   return (
       <React.Fragment>
     <div> 
           <main className="main">
           <ol class="breadcrumb">
       <li class="breadcrumb-item"> <Link to ={process.env.PUBLIC_URL+ "/"}>Home</Link></li>
          <li class="breadcrumb-item">
            View Equipment
          </li>
          </ol>
               <div className="container-fluid">
                   <div className="row">
                      
                       {/* <div className="col-sm-12">
                       <div className="card custom-equipment-card-view">
                       <div className="card-body custom-card-padding">
                       <div className="row">
                           <div className="offset-lg-8 col-lg-4 offset-md-8 col-md-4">
                             <Link to ={process.env.PUBLIC_URL +"/Equipment"}> <button type="button" className="btn btn-info custom-add-btn"><i className="icon-plus"></i>&nbsp;&nbsp;Add Equipment</button> </Link>
                           <br /><br/>
                       </div>
                           </div>
          
                 {equipmentdata}
                 </div>
                 <div className="text-center center-block custom-pagination-btn">
                      {buttondisplay}
                    </div>
               </div>
                       </div> */}
                       <div className="col-sm-12">
                     <div className="card custom-equipment-card-view">
                 <div className="card-body custom-card-padding">
                 <Link to ={process.env.PUBLIC_URL +"/Equipment"}> <button type="button" className="btn btn-info custom-add-btn" style={{float : 'right'}}><i className="icon-plus"></i>&nbsp;&nbsp;Add Equipment</button> </Link>
                 <br /><br />
                 {equipmentdata}
                        </div>
                        <br />
                        <div className="custom-listitem">
                          <ul id="page-numbers">
                            {buttondisplay}
                          </ul>
                        </div>
                {/* {requirementdata} */}
                
               </div>
               {/* <div className="row">
                          <div className="col-lg-3 offset-lg-3 offset-md-3 col-md-3 col-sm-12 col-xs-12 text-center">
                            {prevbtndipsplay}
                          </div>
                          <div className="col-lg-3 col-md-3 col-sm-12 col-xs-12 text-center">
                            {nextbtndisplay}
                          </div>
                        </div> */}
             </div>
                   </div>
               </div>
           </main>
     </div>
     </React.Fragment>
   )
 }
}
export default ViewEquipment
