import React, { Component } from 'react'
import axios from 'axios';
import {Link, withRouter} from 'react-router-dom'
 import authService from '../../services/auth-service';
import { API_URL } from '../../services/url';
import './Profile.css'
export class componentName extends Component {
    constructor(props) {
        super(props)
        this.state = {
          name : sessionStorage.getItem('cname'),
          address : sessionStorage.getItem('address'),
          contactNumber : sessionStorage.getItem('number'),
          contactPerson :sessionStorage.getItem('name'),
          email : sessionStorage.getItem('email')
        }
        this.updatehandler = this.updatehandler.bind(this)
      }
      updatehandler() {
        
      }
    change = e => {
        this.setState ({
            [e.target.name] : e.target.value,
            company : sessionStorage.getItem('companyId')
        })
    }
      onSubmit = async e => {
        console.log(this.state);
        const resp = await axios.post(`${API_URL}/company/update`, {
          
            _id:this.state.company,
            email: this.state.email,
            name: this.state.name,
            contactPerson: this.state.contactPerson,
            address:this.state.address
          }
        
        , axios.defaults.headers.common['w_auth'] = authService.getToken());
        console.log(resp);
        if (resp.data.n !== 0) {
            await sessionStorage.setItem('name', this.state.contactPerson);
            await sessionStorage.setItem('address', this.state.address);
            await sessionStorage.setItem('cname', this.state.name);
            await sessionStorage.setItem('email', this.state.email);
          alert("Your Profile is Updated");
          this.props.history.push('/ViewProfile')
        }
        }
    
    render() {
    return (
        <React.Fragment>
      <div>
        <main className="main">
        <ol class="breadcrumb">
       <li class="breadcrumb-item"> <Link to ={process.env.PUBLIC_URL+ "/"}>Home</Link></li>
          <li class="breadcrumb-item">
            Change Profile
          </li>
          </ol>
            <div className="container-fluid">
                <div className="animated fadeIn">
                    <div className="row">
                  <div className="col-sm-12">
                       <div className="card custom-equipment-card">
                  <div className="card-header">
                    <strong>Profile </strong>
                    <small>&nbsp;&nbsp;</small>
                  </div>
                  <div className="card-body">
                    <form autoComplete = "OFF">
                    <div className="row">
                      <div className="col-sm-4">
                        <div className="form-group">
                          <label for="ccnumber">Company Name*</label>
                          <input className="form-control" id="quantity" type="text" name="name"   value = {this.state.name} onChange = {e => this.change(e)} max="100"/>
                        </div>
                        
                      </div>
                      <div className="col-sm-4">
                        <div className="form-group">
                          <label for="name">Email</label>
                          <input className="form-control" id="quantity" type="email"  name="email" value = {this.state.email} onChange = {e => this.change(e)} max="100"/>
                        
                        </div>
                      </div>
                      
                     <div className="col-md-4">
                        <div className="form-group">
                          <label for="name">Contact Person Name*</label>
                          <input className="form-control" id="name" type="text"  name="contactPerson" value = {this.state.contactPerson} onChange = {e => this.change(e)}/>
                        </div>
                      </div>
                      <div className="col-md-12">
                        <div className="form-group">
                          <label for="name">Address</label>
                          <textarea className="form-control" id="name" type="text"  name="address" value = {this.state.address} onChange = {e => this.change(e)} >
                        </textarea>
                        </div>
                      </div>
                    </div>
                        <br />
    
                   <div className="row">
                        <div className="col-sm-12 text-center center-block">
                            <button type="button" className="btn btn-primary" onClick = {e => this.onSubmit(e)}>Submit</button>
                        </div>
                   </div>
                   </form>
                  </div>
                </div>   
                       </div>     
                    </div>
                </div>
            </div>
        </main>
      </div>
      </React.Fragment>
    )
    }
    }
export default componentName
