import React, { Component } from "react";
import "./Requirement.css";
import { Link } from "react-router-dom";
import axios from "axios";
import authService from "../../services/auth-service";
import { API_URL } from "../../services/url";
export class ViewRequirement extends Component {
  constructor(props) {
    super(props);
    this.state = {
      userdata: [],
      skip: 0,
      limit: 4,
      requirementStatus: ""
    };
    this.nextpagehandler = this.nextpagehandler.bind(this);
    this.prevpagehandler = this.prevpagehandler.bind(this);
  }
  prevpagehandler() {
    console.log("paginationhandler");
    // this.setState({
    //   skip: this.state.skip + this.state.limit
    // });\
    this.state.skip = this.state.skip - this.state.limit;
    let limit = this.state.limit;
    let dataskip = this.state.skip;
    console.log(dataskip);
    axios
      .get(
        `${API_URL}/requirement/list`,
        { params: { skip: this.state.skip, limit: this.state.limit } },
        (axios.defaults.headers.common["w_auth"] = authService.getToken())
      )
      .then(response => {
        console.log(response.data);
        const data = response.data;
        this.setState({ userdata: data });
      })
      .catch(error => {
        console.log(error);
      });
  }
  nextpagehandler() {
    console.log("paginationhandler");
    // this.setState({
    //   skip: this.state.skip + this.state.limit
    // });\
    this.state.skip = this.state.skip + this.state.limit;
    let limit = this.state.limit;
    let dataskip = this.state.skip;
    console.log(dataskip);
    console.log(`${API_URL}/company/list`, {
      params: { skip: dataskip, limit: limit }
    });
    axios
      .get(
        `${API_URL}/requirement/list`,
        { params: { skip: dataskip, limit: limit } },
        (axios.defaults.headers.common["w_auth"] = authService.getToken())
      )
      .then(response => {
        console.log(response.data);
        const data = response.data;
        this.setState({ userdata: data });
      })
      .catch(error => {
        console.log(error);
      });
  }
  componentDidMount() {
    this.handleclick();
  }
  handleclick() {
    axios
      .get(
        `${API_URL}/requirement/list`,
        { params: { skip: this.state.skip, limit: this.state.limit } },
        (axios.defaults.headers.common["w_auth"] = authService.getToken())
      )
      .then(async response => {
       // console.log(response);
       // console.log(response.data);
        const data = response.data;
        await this.setState({ userdata: data });
        console.log("userdata");
        console.log(this.state.userdata);
      })
      .catch(error => {
        console.log(error.response);
      });
  }
  async deletehandler(item) {
    await this.setState({ _id: item._id });
    console.log(this.state._id);
    // console.log(item);
    const response = await axios.delete(
      `${API_URL}/requirement/delete`,
      { data: { _id: item._id } },
      (axios.defaults.headers.common["w_auth"] = authService.getToken())
    );
    console.log(response);
    if (response.data.n === 1) {
      alert("Requirement has been deleted");
      this.handleclick();
      //window.location.reload();
    }
  }
  async updatehandler(item) {
    // console.log(this.state.userdata)
    console.log("requirementstatus", item.requirementStatus);
    if (item.requirementStatus == 1) {
      this.state.requirementStatus = 0;
    }
    console.log("new value here", this.state.requirementStatus);
    // console.log('body', body)
    // console.log("value here", body);
    await this.setState({ _id: item._id });
    console.log(this.state._id);
    console.log(item);
    const response = await axios.post(
      `${API_URL}/requirement/update`,
      { requirementStatus: this.state.requirementStatus, _id: this.state._id },
      (axios.defaults.headers.common["w_auth"] = authService.getToken())
    );
    console.log(response);
    console.log(response.data.n);
    console.log(this.state.requirementStatus);
    if (response.data.n != 0) {
      alert("Requirement has been Deleted");
      this.handleclick();
      //window.location.reload();
    } else {
      alert("Cannot Delete The Requirement");
    }
  }

  render() {
    let buttondisplay;
    if (this.state.skip == 0) {
      buttondisplay = (
        <div>
          <button
            type="button"
            className="btn btn-info"
            onClick={this.prevpagehandler.bind(this)}
            disabled
          >
            Previous
          </button>
          <button
            type="button"
            className="btn btn-info"
            onClick={this.nextpagehandler.bind(this)}
          >
            Next
          </button>
        </div>
      );
    } else {
      buttondisplay = (
        <div>
          <button
            type="button"
            className="btn btn-info"
            onClick={this.prevpagehandler.bind(this)}
          >
            Previous
          </button>
          <button
            type="button"
            className="btn btn-info"
            onClick={this.nextpagehandler.bind(this)}
          >
            Next
          </button>
        </div>
      );
    }
   
    let requirementdata;
    if (this.state.userdata.length === 0) {
      requirementdata = (
        <div>
          <center>
            <h4>
              <strong>There is No Requirement Added Yet.</strong>
            </h4>
          </center>
        </div>
      );
    } else {
      let sr=this.state.skip
      requirementdata = (
        <div>
          <table className="table table-responsive-sm table-bordered custom-requirement-table">
            <thead className="thead-dark">
              <tr>
                <th scope="col">Sr. No.</th>
                {/*<th scope="col">Category</th> */}
                <th scope="col">Equipment Name</th>
                <th scope="col">Capacity</th>
                <th scope="col">Location</th>
                <th scope="col">Pincode</th>
                <th scope="col">Tenure(Days)</th>
                <th scope="col">Date of Creation</th>
                <th scope="col">Action</th>
              </tr>
            </thead>
            <tbody>
              {
                this.state.userdata.length ? (
                  
                  this.state.userdata.map(function (item, _id) {
                  let name = item.subcategory
                    ? item.subcategory.name
                    : "no data";
                    
                  return (
                    
                    <tr key={_id}>
                      <td>{++sr}</td>
                      {/*<td>{item.category.name || "NO DATA"}</td> */}
                      <td>{name}</td>
                      <td>{item.capacity || "NO DATA"}</td>
                      <td>{item.location || "NO DATA"}</td>
                      <td>{item.pinCode || "NO DATA"}</td>
                      <td>{item.tenure || "NO DATA"} Days</td>
                      <td>
                        {new Date(item.createdAt)
                          .toString()
                          .split(" ")
                          .slice(0, 4)
                          .join(" ") || "NO DATA"}
                      </td>
                      <td>
                        <button
                          type="button"
                          className="btn btn-danger custom-btn"
                          onClick={this.updatehandler.bind(this, item)}
                          
                        >
                          <i className="icon-trash icons" />
                        </button>
                        {/* <button
                          type="button"
                          className="btn btn-warning custom-btn"
                          onClick={this.updatehandler.bind(this, item)}
                          
                        >
                          <i className="icon-pencil icons" />
                        </button> */}
                      </td>
                    </tr>
                  );
                }, this)
              ) : (
                <span>No Data Present....</span>
              )}
            </tbody>
          </table>
        </div>
      );
    }
    return (
      <React.Fragment>
        <div>
          <main className="main">
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
                {" "}
                <Link to={process.env.PUBLIC_URL + "/"}>Home</Link>
              </li>
              <li class="breadcrumb-item">View Requirement</li>
            </ol>
            <div className="container-fluid">
              <div className="row">
                <div className="col-sm-12">
                  <div className="card custom-equipment-card-view">
                    {/* <div className="row">
                      <div className="offset-lg-8 col-lg-4 offset-md-8 col-md-4">
                        <Link to={process.env.PUBLIC_URL + "/Requirement"}>
                          {" "}
                          <button
                            type="button"
                            className="btn btn-info custom-add-btn"
                          >
                            <i className="icon-plus" />
                            &nbsp;&nbsp;Add Requirement
                          </button>{" "}
                        </Link>
                        <br />
                        <br />
                      </div>
                    </div> */}
                    {/* <div className="card-header table-bordered">
                 <i className="fa fa-align-justify"></i> Requirements</div> */}
            
                 <div className="card-body custom-card-padding">
                 <Link to={process.env.PUBLIC_URL + "/Requirement"}>
                          {" "}
                          <button
                            type="button"
                            className="btn btn-info custom-add-btn"
                          style={{float : 'right'}}>
                            <i className="icon-plus" />
                            &nbsp;&nbsp;Add Requirement
                          </button>{" "}
                        </Link>
                        <br /><br /><br />
                      {requirementdata}
                      
                    </div>
                    <div className="text-center center-block custom-pagination-btn">
                      {buttondisplay}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </main>
        </div>
      </React.Fragment>
    );
  }
}
export default ViewRequirement;
