import React, { Component } from 'react';
import './Login.css';
import axios from 'axios';
 import authService from '../../services/auth-service';
import { API_URL } from '../../services/url';

class AddLogin extends Component {
	constructor(props)
	{
		super(props);
		this.state = {
			contactNumber: "",
		  password: "",
		}	
		
	}
	change = e => 
	{
	 this.setState({
		 [e.target.name] : e.target.value
	 })
	}
	onSubmit = e =>
	{
		e.preventDefault();
		this.setState({
		    contactNumber : "",
		    password: ""
    })
        axios.post(`${API_URL}/company/login`,this.state)
        .then(async res =>{ 
                    console.log(res.data)
                    if(res.data.loginSuccess){
                     await authService.saveToken(res.data.w_auth);
                    //  await localStorage.setItem('companyId',res.data.companyId);
                    //  await localStorage.setItem('number',res.data.user.contactNumber);
                    //  await localStorage.setItem('name', res.data.user.name);
                    //   await localStorage.setItem('address', res.data.user.company.address);
                    //   await localStorage.setItem('cname', res.data.user.company.name);
                    //  await localStorage.setItem('email', res.data.user.email);
                    await sessionStorage.setItem('companyId',res.data.companyId);
                     await sessionStorage.setItem('number',res.data.user.contactNumber);
                     await sessionStorage.setItem('name', res.data.user.name);
                      await sessionStorage.setItem('address', res.data.user.company.address);
                      await sessionStorage.setItem('cname', res.data.user.company.name);
                      await sessionStorage.setItem('email', res.data.user.email);
                      await sessionStorage.setItem('role', res.data.user.role);
                     alert("Login Successful");
                   window.location.reload();
                  }else{
                        alert(res.data.message);
				            }
               })
		
		
	}
    render() {
		//const onRouteChange = this.props.onRouteChange;
        return (
            <main className="main">
             <div className="form-example-area custom-form-example">
             <div class="container">
      <div class="row justify-content-center">
        <div class="col-md-8">
          <div class="card-group">
            <div class="card p-4 custom-login-card">
              <div class="card-body">
              <div className="center-block text-center"><i class="fa fa-user-circle fa-5x" aria-hidden="true"></i></div>
                <h1>Login</h1>
                <p class="text-muted">Sign In to your account</p>
                <div class="input-group mb-3">
                  <div class="input-group-prepend">
                    <span class="input-group-text">
                      <i class="icon-user"></i>
                    </span>
                  </div>
                  <input class="form-control" type="text" placeholder="Enter Mobile Number" name="contactNumber" value = {this.state.contactNumber} onChange = {e =>this.change(e)} required/>
                </div>
                <div class="input-group mb-4">
                  <div class="input-group-prepend">
                    <span class="input-group-text">
                      <i class="icon-lock"></i>
                    </span>
                  </div>
                  <input class="form-control" type="password" placeholder="Enter Password" name="password" value = {this.state.password} onChange = { e => this.change(e)} />
                </div>
                <div class="row">
                  <div class="col-6">
                    <button class="btn btn-primary px-4" onClick = {e => this.onSubmit(e)} type="button">Login</button>
                  </div>
                          </div>
                          <h4>Feel Free To Contact Us</h4><h2><strong>0731-4984775</strong><br />
                          <strong>+91 70671-17305</strong></h2>

              </div>
            </div>
          </div>
                  </div>
                  
      </div>
              </div>
              <br />
                  <br/>
			 </div>
            </main>
        );
    }
}

export default AddLogin;