import React, { Component } from 'react'
import './Feedback.css'
import { Link } from 'react-router-dom'
import axios from 'axios';
 import authService from '../../services/auth-service';
 import { API_URL } from '../../services/url';
export class componentName extends Component {
    constructor(props) {
        super(props)
        this.state = {
          comment: "",
          company:sessionStorage.getItem('companyId')
        }
    }
    change = e => {
        this.setState ({
            [e.target.name] : e.target.value,
             
        })
    }
        onSubmit = e => {
          let body = {
              comment:this.state.comment,
              company: sessionStorage.getItem('companyId'),
              companyName : sessionStorage.getItem('cname')
            }
            e.preventDefault();
            console.log('body here', body);
            axios.post(`${API_URL}/feedback/save`,body,axios.defaults.headers.common['w_auth'] = authService.getToken())
        .then(function(response){ 
           console.log(response);
           console.log(response.status)
           if(response.status === 200)
           {
             alert('Feedback Submitted.')
             this.props.history.push('/Notification')
           }
        }.bind(this))
        .catch(error => {
            console.log(error.response);
          })
        }

  render() {
    return (
        <React.Fragment>
      <div>
        <main className="main">
        <ol class="breadcrumb">
       <li class="breadcrumb-item"> <Link to ={process.env.PUBLIC_URL+ "/"}>Home</Link></li>
          <li class="breadcrumb-item">
            Add Equipment
          </li>
          </ol>
            <div className="container-fluid">
                <div className="animated fadeIn">
                    <div className="row">
                       <div className="col-sm-12">
                       <div className="card custom-equipment-card">
                  <div className="card-header">
                    <strong>Feedback </strong>
                    <small>&nbsp;&nbsp;Form</small>
                  </div>
                  <div className="card-body">
                    <form autoComplete = "OFF">
                    <div className="row">
                      <div className="col-sm-12">
                        <div className="form-group">
                          <label for="ccnumber">Enter Feedback*</label>
                            <textarea className="form-control" value={this.state.comment} name="comment" onChange={e => this.change(e)}></textarea>                        </div>
                        
                      </div>
                    </div>
                        <br />

                   <div className="row">
                        <div className="col-sm-12 text-center center-block">
                            <button type="button" className="btn btn-primary" onClick = {e => this.onSubmit(e)}>Submit</button>
                        </div>
                   </div>
                   </form>
                  </div>
                </div>   
                       </div>     
                    </div>
                </div>
            </div>
        </main>
      </div>
      </React.Fragment>
    )
  }
}

export default componentName
