import React, { Component } from 'react';

import './App.css';
import Signin from './Components/Login/AddLogin';
import Register from './Components/Login/Register';
import {Route, Redirect} from 'react-router-dom';
import Header from './Components/Header/Header';
import Sidebar from './Components/Sidebar/Sidebar';
import Home from './Components/Home/Home';

import { ViewEquipment } from './Components/Equipment/ViewEquipment';
import { Equipment } from './Components/Equipment/Equipment';
import { ViewCompany } from './Components/Company/ViewCompany';
import { Company } from './Components/Company/Company';
import { ViewRequirement } from './Components/Requirement/ViewRequirement';
import { Requirement } from './Components/Requirement/Requirement';
import { Notification } from './Components/Notification/Notification';
import ViewProfile from './Components/Profile/profile';
import  Feedback  from './Components/Feedback/Feedback';
import Footer from './Components/Footer/Footer';
import EditProfile from './Components/Profile/EditProfile';
import { Operator } from './Components/Operator/Operator';
import { ViewOperator } from './Components/Operator/ViewOperator';
import { ViewOperatorRequirement } from './Components/Operator/ViewOperatorRequirement';
class App extends Component {
  constructor() {
    super();
    this.state = {
      route: 'signin',
      isSignedIn: false,
    }
  
  }


  onRouteChange = (route) => {
    if (route === 'signout') {
      this.setState({isSignedIn: false})
    } else if (route === 'home') {
      this.setState({isSignedIn: true})
    }
    this.setState({route: route});
  }
  render() {
    const { isSignedIn, route } = this.state;
    return (
      <div>
       
      {/* <div isSignedIn={isSignedIn} onRouteChange={this.onRouteChange} > */}
        <div isSignedIn={isSignedIn} onRouteChange={this.onRouteChange} />
        { sessionStorage.getItem('w_auth')&&sessionStorage.getItem("role")!=2
        ?
   
       <div className="app header-fixed sidebar-fixed aside-menu-fixed sidebar-lg-show">
         <Header/>
         <Sidebar />  
        
         <Route path={process.env.PUBLIC_URL +"/"} exact={true} component = {Notification} /> 
         <Route path = {process.env.PUBLIC_URL +"/ViewEquipment"} exact = {true} component = {ViewEquipment}/>
         <Route path = {process.env.PUBLIC_URL +"/Equipment"} exact = {true} component = {Equipment} />
         <Route path = {process.env.PUBLIC_URL +"/ViewCompany"} exact = {true} component = {ViewCompany}/>
         <Route path = {process.env.PUBLIC_URL +"/Company"} exact = {true} component = {Company} />
         <Route path = {process.env.PUBLIC_URL +"/ViewRequirement"} exact = {true} component = {ViewRequirement}/>
         <Route path = {process.env.PUBLIC_URL +"/Requirement"} exact = {true} component = {Requirement} />
            <Route path={process.env.PUBLIC_URL + "/Notification"} exact={true} component={Notification} />
            <Route path={process.env.PUBLIC_URL + "/ViewProfile"} exact={true} component={ViewProfile} />
            <Route path={process.env.PUBLIC_URL + "/Feedback"} exact={true} component={Feedback} />
            <Route path = {process.env.PUBLIC_URL +"/EditProfile"} exact = {true} component = {EditProfile} />
            <Route path = {process.env.PUBLIC_URL +"/Operator"} exact = {true} component = {Operator} />
            <Route path={process.env.PUBLIC_URL + "/ViewOperator"} exact={true} component={ViewOperator} />
            <Route path ={process.env.PUBLIC_URL + "/viewOperatorRequirement"} exact = {true} component = {ViewOperatorRequirement} />
         </div>
      : (
        sessionStorage.getItem('w_auth')&&sessionStorage.getItem("role")==2
             ?<div className="app header-fixed sidebar-fixed aside-menu-fixed sidebar-lg-show">
             <Header/>
                <Sidebar /> 
                <Route path={process.env.PUBLIC_URL +"/"} exact={true} component = {ViewOperatorRequirement} />   
                  {/* <Route path={process.env.PUBLIC_URL + "/viewOperatorRequirement"} exact={true} component={} /> */}
                  </div> 
              : <Signin  onRouteChange={this.onRouteChange}/>
              
            )
        }
        {/* </div> */}
        <Footer />
      </div>
    );
  }
}

export default App;
