import React, { Component } from 'react'
import './Company.css'
import {Link} from 'react-router-dom'
import axios from 'axios'
export class ViewCompany extends Component {
    constructor(props)
    {
        super(props)
        this.state = {
            userdata : []
        }
        
    }
    componentDidMount() {
        this.handleclick();
    }
    handleclick() {        
        console.log('website on load content component did mount')
               axios.get('admin/view')
               .then(response => {
               // response.json().then((resp) => { 
                       // console.log(resp.admindata)
                       // this.setState({ userdata:resp.admindata})
                       // // this.parseJSON(this.state)
                       console.log(response.userdata.admindata);
                       const data = response.userdata.admindata;
                       this.setState({ data })
                       }) .catch(error => {
                       console.log(error);
                       })

    }
    

 render() {
   return (
       <React.Fragment>
     <div> 
           <main className="main">
               <div className="container-fluid">
                   <div className="row">
                      
                       <div className="col-sm-12">
                       <div className="card custom-company-card-view">
                       <div className="row">
                           <div className="offset-lg-8 col-lg-4 offset-md-8 col-md-4">
                             <Link to ={process.env.PUBLIC_URL +"/Company"}> <button type="button" className="btn btn-info custom-add-btn"><i className="icon-plus"></i>&nbsp;&nbsp;Add Company</button> </Link>
                           <br /><br/>
                       </div>
                           </div>
                 <div className="card-header table-bordered">
                   <i className="fa fa-align-justify"></i> Companies</div>
                 <div className="card-body">
                   <table className="table table-responsive-sm table-bordered">
                     <thead className="thead-dark">
                       <tr>
                         <th>Username</th>
                         <th>Date registered</th>
                         <th>Role</th>
                         <th>Status</th>
                       </tr>
                     </thead>
                     <tbody>
                       <tr>
                         <td>Samppa Nori</td>
                         <td>2012/01/01</td>
                         <td>Member</td>
                         <td>
                           <span className="badge badge-success">Active</span>
                         </td>
                       </tr>
                       <tr>
                         <td>Estavan Lykos</td>
                         <td>2012/02/01</td>
                         <td>Staff</td>
                         <td>
                           <span className="badge badge-danger">Banned</span>
                         </td>
                       </tr>
                       <tr>
                         <td>Chetan Mohamed</td>
                         <td>2012/02/01</td>
                         <td>Admin</td>
                         <td>
                           <span className="badge badge-secondary">Inactive</span>
                         </td>
                       </tr>
                       <tr>
                         <td>Derick Maximinus</td>
                         <td>2012/03/01</td>
                         <td>Member</td>
                         <td>
                           <span className="badge badge-warning">Pending</span>
                         </td>
                       </tr>
                       <tr>
                         <td>Friderik Dvid</td>
                         <td>2012/01/21</td>
                         <td>Staff</td>
                         <td>
                           <span className="badge badge-success">Active</span>
                         </td>
                       </tr>
                     </tbody>
                   </table>
                   <ul className="pagination">
                     <li className="page-item">
                       <a className="page-link" href="#">Prev</a>
                     </li>
                     <li className="page-item active">
                       <a className="page-link" href="#">1</a>
                     </li>
                     <li className="page-item">
                       <a className="page-link" href="#">2</a>
                     </li>
                     <li className="page-item">
                       <a className="page-link" href="#">3</a>
                     </li>
                     <li className="page-item">
                       <a className="page-link" href="#">4</a>
                     </li>
                     <li className="page-item">
                       <a className="page-link" href="#">Next</a>
                     </li>
                   </ul>
                 </div>
               </div>
                       </div>
                   </div>
               </div>
           </main>
     </div>
     </React.Fragment>
   )
 }
}

export default ViewCompany
