import React, { Component } from 'react'
import './Header.css'
import './flag-icon-css/css/flag-icon.css'
import './simple-line-icons/css/simple-line-icons.css'
import './coreui-icons.css';
import {Link, withRouter } from 'react-router-dom'

export class Header extends Component {
 
  onLogout = e =>
  {
    e.preventDefault();
    sessionStorage.removeItem('comapany_id');
    sessionStorage.removeItem('w_auth');
    sessionStorage.clear();
        this.props.history.push('/')
      window.location.reload() 
      }

  render() {
    return (
        <React.Fragment>
      <header className="app-header navbar">
      <button className="navbar-toggler sidebar-toggler d-lg-none mr-auto" type="button" data-toggle="sidebar-show">
        <span className="navbar-toggler-icon"></span>
      </button>
      <a className="navbar-brand" href="/">
        <img src={require('../img/brand/yantranant.png')} className="navbar-brand-full" width="89" height="80" alt="" />
        <img src={require('../img/brand/yantranant.png')} className="navbar-brand-minimized" width="30" height="30" alt="" />
      </a>
      <button className="navbar-toggler sidebar-toggler d-md-down-none" type="button" data-toggle="sidebar-lg-show">
        <span className="navbar-toggler-icon"></span>
      </button>
      <ul className="nav navbar-nav ml-auto">
        <li className="nav-item dropdown">
          <a className="nav-link" data-toggle="dropdown" href="#" role="button" aria-haspopup="true" aria-expanded="false">
            <img className="img-avatar" src={require('../../assets/img/avatar.png')} alt="avatar" />
          </a>
          <div className="dropdown-menu dropdown-menu-right">
            <div className="dropdown-header text-center">
              <strong>Account</strong>
                </div>
                <Link to ={process.env.PUBLIC_URL +"/ViewProfile"}>
                <a className="dropdown-item">
                  <i className="fa fa-user"></i>Profile</a>
                  </Link>
            

                
            <a className="dropdown-item" href="/" onClick = {e => this.onLogout(e)}>
                  <i className="fa fa-lock"></i>Logout
            </a>
          </div>
        </li>
      </ul>
     </header>
   
      </React.Fragment>
    )
  }
}

export default withRouter(Header)
