import React, { Component } from "react";
import "./operator.css";
import { Link } from "react-router-dom";
import axios from "axios";
import authService from "../../services/auth-service";
import { API_URL } from "../../services/url";
export class Operator extends Component {
  constructor(props) {
    super(props);
    this.state = {
      subcategory: "",
      company: "",
      location: "",
      pinCode: "",
      requirementStatus: "1",
      userdata: [],
      subcatdataonclick: [],
      tenure: "",
      city: "",
      salary: "",
      date_of_requirement: "",
      description: "",
      company_status_btn: false,
      company_status: ""
    };
  }
  componentDidMount() {
    this.siteclick();
  }
  handleclick() {
    axios
      .get(
        `${API_URL}/category/list`,
        (axios.defaults.headers.common["w_auth"] = authService.getToken())
      )
      .then(async response => {
        console.log(response);
        console.log(response.data);
        const data = response.data;
        await this.setState({ userdata: data });
        console.log("userdata");
        console.log(this.state.userdata);
      })
      .catch(error => {
        console.log(error.response);
      });
  }
  siteclick() {
    console.log(this.state.category);
    // console.log(this.state.value);
    const sitemeet = this.state.category;
    console.log("sitemeet id");
    console.log(sitemeet);
    axios
      .get(
        `${API_URL}/subcategory/list`,
        (axios.defaults.headers.common["w_auth"] = authService.getToken())
      )
      .then(async response => {
        console.log(response);
        const data = response.data;
        await this.setState({ subcatdataonclick: data });
        console.log("subcatdataonclick");
        console.log(this.state.subcatdataonclick);
      })
      .catch(error => {
        console.log(error.response);
      });
  }
  change = e => {
    this.setState({
      [e.target.name]: e.target.value,
      company: localStorage.getItem("companyId")
    });
  };
  onSubmit = async e => {
    if (this.state.pinCode.length === 6 && this.state.pinCode > 99999) {
      if (this.state.tenure <= 1000 && this.state.tenure > 0) {
        e.preventDefault();

        console.log("hi");
        await this.setState({
          capacity: this.state.capacity + " " + this.state.unit
        });
        console.log(this.state);
        let body = {
          subcategory: this.state.subcategory,
          location: this.state.location,
          pinCode: this.state.pinCode,
          tenure: this.state.tenure,
          salary: this.state.salary,
          startDate: this.state.date_of_requirement,
          operatorRequestStatus: 1,
          company: sessionStorage.getItem("companyId"),
          description: this.state.description
        };
        axios
          .post(
            `${API_URL}/operator/saveOperatorRequest`,
            body,
            (axios.defaults.headers.common["w_auth"] = authService.getToken())
          )
          .then(
            function(response) {
              console.log(response);
              console.log(response.status);
              if (response.status === 200) {
                this.state.company_status = response.status;
                this.setState({
                  company_status_btn: true
                });
                alert("Operator Requirement Has Been Added.");
                this.props.history.push("/ViewOperator");
              }
            }.bind(this)
          )
          .catch(error => {
            console.log(error.response);
          });
      } else {
        alert(
          "Maximum tenure days limit 1000 days, Please insert value less than 1000 "
        );
      }
    } else {
      alert("Pincode should be 6 digits");
    }
  };

  render() {
    let sbmtbtnstatus, sbmtbtntext;
    sbmtbtnstatus = (
      <button
        type="button"
        className="btn btn-primary"
        onClick={e => this.onSubmit(e)}
      >
        Submit
      </button>
    );
    if (this.state.company_status_btn) {
      if (this.state.company_status === 200) {
        sbmtbtnstatus = (
          <button type="button" className="btn btn-primary" disabled>
            Submit
          </button>
        );
        sbmtbtntext = <p>Form Has Been Submitted.</p>;
      }
    }
    return (
      <React.Fragment>
        <div>
          <main className="main">
            <ol class="breadcrumb">
              <li class="breadcrumb-item">
                {" "}
                <Link to={process.env.PUBLIC_URL + "/"}>Home</Link>
              </li>
              <li class="breadcrumb-item">Add Operator</li>
            </ol>
            <div className="container-fluid">
              <div className="animated fadeIn">
                <div className="row">
                  <div className="col-sm-12">
                    <div className="card custom-equipment-card">
                      <div className="card-header">
                        <strong>Operator </strong>
                        <small>&nbsp;&nbsp;Form</small>
                      </div>
                      <div className="card-body">
                        <form autoComplete="OFF">
                          <div className="row">
                            <div className="col-sm-6">
                              <div className="form-group">
                                <label for="ccnumber">Equipment Name*</label>
                                <select
                                  className="form-control"
                                  name="subcategory"
                                  value={this.state.subcategory}
                                  onChange={e =>
                                    this.setState({
                                      subcategory: e.target.value
                                    })
                                  }
                                >
                                  <option>choose....</option>
                                  {this.state.subcatdataonclick ? (
                                    this.state.subcatdataonclick.map(function(
                                      item,
                                      id
                                    ) {
                                      return (
                                        <option key={id} value={item.id}>
                                          {item.name}
                                        </option>
                                      );
                                    })
                                  ) : (
                                    <span>Data is loading....</span>
                                  )}
                                </select>
                              </div>
                            </div>
                            {/* <div className="col-sm-3">
                            <div className="form-group">
                              <label for="name">Enter City*</label>
                              <input className="form-control" id="capacity" type="text" placeholder="Enter city" name="city" value = {this.state.city} onChange = {e => this.change(e)} />
                            {/* <small>Enter Number Only</small> 
                            </div>
                            </div> */}
                            <div className="col-sm-3">
                              <div className="form-group">
                                <label for="name">Tenure(Days)*</label>
                                <input
                                  className="form-control"
                                  id="tenure"
                                  type="number"
                                  placeholder="tenure"
                                  name="tenure"
                                  value={this.state.tenure}
                                  onChange={e => this.change(e)}
                                  max="1000"
                                />
                              </div>
                            </div>
                            <div className="col-sm-3">
                              <div className="form-group">
                                <label for="name">Enter Salary*</label>
                                <input
                                  className="form-control"
                                  id="salary"
                                  type="number"
                                  placeholder="Enter salary"
                                  name="salary"
                                  value={this.state.salary}
                                  onChange={e => this.change(e)}
                                />
                                <small>Enter Number Only</small>
                                {/* <select className="form-control" name="unit" value={this.state.unit}  onChange={(e) => this.setState({unit: e.target.value})}>
                                <option>choose....</option>
                            
                                  <option value="Tonne">Tonne</option>
                                  <option value="Cum">Cum</option>
                                  <option value="Meter">Meter</option>
                                  <option value="Kgs">Kgs</option>
                                  <option value="Feet">Feet</option>
                                  <option value="Tyres">Tyres</option>
                                  <option  value="Litre">Litre</option>
                               
    
                              </select> */}
                              </div>
                            </div>
                          </div>
                          <div className="row">
                            {/* <div className="col-sm-3">
                        <div className="form-group">
                          <label for="name">Tenure(Days)*</label>
                          <input className="form-control" id="tenure" type="number" placeholder="tenure" name="tenure" value = {this.state.tenure} onChange = {e => this.change(e)} max="1000"/>
                        </div>
                      </div> */}

                            <div className="col-sm-4">
                              <div className="form-group">
                                <label for="name">Enter Location*</label>
                                <input
                                  className="form-control"
                                  id="name"
                                  type="text"
                                  placeholder="Enter Location"
                                  name="location"
                                  value={this.state.location}
                                  onChange={e => this.change(e)}
                                />
                              </div>
                            </div>

                            <div className="col-sm-4">
                              <div className="form-group">
                                <label for="name">Enter Pincode*</label>
                                <input
                                  className="form-control"
                                  id="name"
                                  type="number"
                                  placeholder="Enter Pincode"
                                  name="pinCode"
                                  value={this.state.pinCode}
                                  onChange={e => this.change(e)}
                                  min="99999"
                                  max="999999"
                                />
                                <small>Enter Number Only</small>
                              </div>
                            </div>

                            <div className="col-sm-4">
                              <div className="form-group">
                                <label for="name">
                                  Enter Date Of Requirement*
                                </label>
                                <input
                                  className="form-control"
                                  id="date"
                                  type="date"
                                  placeholder="Enter date"
                                  name="date_of_requirement"
                                  value={this.state.date_of_requirement}
                                  onChange={e => this.change(e)}
                                  min="99999"
                                  max="999999"
                                />
                                <small>Enter Number Only</small>
                              </div>
                            </div>
                          </div>
                          <br />
                          <div className="col-sm-12">
                            <div className="form-group">
                              <label for="name">Description</label>
                              <textarea
                                className="form-control"
                                id="name"
                                type="text"
                                placeholder="Enter description"
                                name="description"
                                value={this.state.description}
                                onChange={e => this.change(e)}
                              />
                            </div>
                          </div>

                          <div className="row">
                            <div className="col-sm-12 text-center center-block">
                              {sbmtbtnstatus}
                              <br />
                              {sbmtbtntext}
                            </div>
                          </div>
                        </form>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </main>
        </div>
      </React.Fragment>
    );
  }
}

export default Operator;
