import React, { Component } from 'react'
import './Company.css'
import {Link} from 'react-router-dom'
import axios from 'axios'
export class Company extends Component {
    constructor(props) {
        super(props)
        this.state = {
            comp_name : "",
            comp_desc : ""
        }
        this.submit = this.submit.bind(this)
    }
    change = e => {
        this.setState ({
            [e.target.name] : e.target.value
        })
    }
        submit = e => {
            e.preventDefault();
            console.log(this.state);
            axios.post('https://rocky-cove-39377.herokuapp.com/admin/add', this.state)
        .then(function(response){ 
           console.log(response);
        })
        .catch(error => {
            console.log(error);
          })

        }

  render() {
    return (
        <React.Fragment>
      <div>
        <main className="main">
            <div className="container-fluid">
                <div className="animated fadeIn">
                    <div className="row">
                       <div className="col-sm-12">
                       <div className="card custom-company-card">
                  <div className="card-header">
                    <strong>Company </strong>
                    <small>&nbsp;&nbsp;Form</small>
                  </div>
                  <div className="card-body">
                    <div className="row">
                      <div className="col-sm-12">
                        <div className="form-group">
                          <label for="name">Enter Company Name</label>
                          <input className="form-control" id="name" type="text" placeholder="Enter your name" name="comp_name" value = {this.state.comp_name} onChange = {e => this.change(e)}/>
                        </div>
                      </div>
                    </div>
                   
                    <div className="row">
                      <div className="col-sm-12">
                        <div className="form-group">
                          <label for="ccnumber">Enter Company Description</label>
                          <textarea className="form-control" name="comp_desc" value = {this.state.comp_desc} onChange = {e => this.change(e)}></textarea>
                        </div>
                      </div>
                    </div>
                   
                   <div className="row">
                        <div className="col-sm-12 text-center center-block">
                            <button type="button" className="btn btn-primary" onClick = {this.submit}>Submit</button>
                        </div>
                   </div>
                  </div>
                </div>   
                       </div>     
                    </div>
                </div>
            </div>
        </main>
      </div>
      </React.Fragment>
    )
  }
}

export default Company
